import { useState, useEffect, useRef, React } from 'react';
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import LoginButton from "../ui/LoginButton";
import MenuButton from "../ui/MenuButton";
import {LoginToken} from "../reference/GlobalVariable";

const Wrapper = styled.div`
    position: fixed;
    z-index: 3;
    top: 0; left: 0;
    padding: 15px; //  16
    width: calc(100% - 30px); //  100, 16*2 
    min-width: 2000px; //   
    height: 70px;
    color: #CFFD5D; //  
    background: #100E1B; //  
`;

const CompanyName = styled.div`
    width: 1500px; //   50
    font-size: 24px; //  
    font-weight: bold; //  
    text-align: left; //  , 
    display: inline-block; //  ,    
`;

const LoginMenu = styled.div`
    width: 50%; //   50
    font-size: 24px; //  
    font-weight: bold; //  
    text-align: left; //  , 
    display: inline-block;
`;

const SelectMenu = styled.div`
    font-size: 24px;
    font-weight: bold;
    //align-items: center;
    //justify-content: center;
    //text-align: center;
`;

function MainPageHeader(props) {
    const navigate = useNavigate();
    const [change, setchange] = useState(false);

    const handleChangeValue = () => {
        //setchange((change) => !change);
        if(LoginToken.data == 1)        {
            LoginToken.useData = 0;
        }
        else{
            LoginToken.useData = 1;
        }
    }   

    console.log('[MainPageHeader]');
    console.log('LoginToken.data: ', LoginToken.data);
    
    return (
        <Wrapper>
            <CompanyName>
                ,  !  &nbsp;&nbsp;&nbsp;&nbsp;                
                <LoginButton
                    title='/'
                    onClick={() => {
                        navigate("/");
                        handleChangeValue();
                        window.location.reload();
                        //console.log("/");
                    }}
                />
            </CompanyName>
            <LoginMenu>                   
            </LoginMenu>

            <hr/>
        </Wrapper>        
    );
}

export default MainPageHeader;