import { useState, useEffect, useRef, React } from 'react';
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import MenuArrange from './MenuArrange';
import ViewData from "./ViewData";
import LoginPage from "./LoginPage";
import {LoginToken} from "../reference/GlobalVariable";

const Wrapper = styled.div`
    width: calc(100% - 0px); //  100, 16*2 
    min-width: 2000px; //   
    height: calc(100vh - 0px);
    //min-height: 2000px; //   
    margin-right: 15px;
    font-size: 24px; //  
    color: #CFFD5D; //  
    background: #100E1B; //  
`;

const MenuPart = styled.div`
    position: fixed;
    z-index: 3;
    top: 100px;
    padding-left: 15px; //  15
    padding-right: 15px; //  15

    width: calc(100% - 30px);
    min-width: 1000px; //   
    height: 70px;
    font-size: 8px; //    
    font-weight: bold; //  
    background: #100E1B; //  
`;

function MainPageContainer(props) {
    const [change, setchange] = useState(false);
    const {_State} = props;
    
    const handleChangeValue_upper = () => {
        setchange((change) => !change);
    }   

    console.log("[MainPageContainer]");
    console.log("LoginToken", LoginToken.data);
    console.log('_state: ', _State);
    //  
    if (LoginToken.data!=1){
        console.log("[ ]");
        return (
            <Wrapper>
                <LoginPage    
                    handleButtonClick_upper = {handleChangeValue_upper}>            
                </LoginPage>
            </Wrapper>
        );
    }
    //  
    else{
        console.log("[ ]");
        return (
            <Wrapper>
                <MenuPart>
                    <MenuArrange
                        handleButtonClick_upper = {handleChangeValue_upper}>                      
                    </MenuArrange>
                </MenuPart>
                    <ViewData>
                    </ViewData>           
            </Wrapper>
        );
    }


    
}

export default MainPageContainer;